.wrapper{
    font-size: 1.4em;

    width: 100%;
    border: 1px solid var(--border-color);
    background-color: var(--secondary-color);
    /*padding: 1em;*/
    padding: 6vh 1vw 20vh;
    border-radius: 0.6em;
    text-align: center;

    animation-name: show_message;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease;

    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5em;

}
.wrapper input{
    text-align: center;
}

.row_btn{
    display: flex;
    align-items: center;
    gap: 0.3em;
}