.search {
  --row-height:2.4em;
  display: flex;
  align-items: flex-start;
  height: var(--row-height);

  width: 62vw;
  position: relative;
  /*border: 1px solid lawngreen;*/
}

.context {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.6s ease;
  margin-right: 0.5em;
  display: flex;
  flex-direction: column;
  background: var(--heading-color);

  z-index: 1;
}

.context.open {
  max-height: 40vh;
}

.filter_str,
.filter_key {
  margin-bottom: 0.1em;
  height: var(--row-height);
}


.input_container {
  /*!*position: relative; */
}

.input_container input[type="text"] {
  font-size: 1em;
  line-height: var(--row-height);
}

