.wrapper {
    padding: 0.2em;
}

.row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid var(--border-color);
    margin-bottom: 0.1em;
    border-radius: 0.2em;
    width: 100%;
    padding: 0.1em 0.2em 0;
    color: var(--text-color);
    min-height: var(--row-min-height);

}

.row button {
    color: var(--secondary-color);
    cursor: pointer;
}

