.title {
    display: flex;
    padding: 0 0.2em;
    font-size: 1.1em;
    /*justify-content: space-between;*/
    align-items: center;
    width: 100%;
    border-radius: inherit;
    background-color: inherit;
}

.group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.2em;
    font-style: italic;
    font-weight: 600;
    line-height: 1.8em;
    /*max-width: 80%;*/

    border-radius: 0.5em;
    margin-bottom: 0.2em;
    padding-left: 0.2em;
    width: 100%;
    display: flex;
    position: relative;

}

.total_price {
    display: flex;
    align-items: center;
    color: var(--error-color);
}
.send{
    background: blue;
}
.receive{
    color:var(--primary-color);
}

.sub_title {
    padding-left: 1.2em;
}

.name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.name div {
    line-height: 0.9em;
}

.column {
    position: relative;
    width: 35%;
    border-radius: inherit;
    background-color: inherit;
    color: var(--heading-color);

}


.stock {
    padding: 0 0.4em;
    border-bottom-right-radius: inherit;
    text-align: right;
    background-color: inherit;
    font-size: 1.5em;
}

.quantity {
    position: absolute;
    top: 0;
    left: -0.8em;
    background-color: var(--heading-color);
    border: 2px solid var(--border-color);
    font-size: 1.5em;
    border-radius: 50%;
    padding: 0.15em;
    opacity: 0.6;
    color: var(--secondary-color);
}

.marker {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;
    top: -0.1em;
    left: -1em;
    opacity: 0.8;
    line-height:0;
    font-size: 0.6em;
}

.group_button {
    display: flex;
    gap: 0.2em;
    justify-content: space-between;
}
