.error_display {
    display: flex;
    justify-content: center;
    z-index: 1;
}

.context {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.6s ease;
    margin-right: 0.5em;
    display: flex;
    /*background: var(--heading-color);*/
    background: inherit;
    z-index: 6;
    gap: 1em;
    align-items: center;
    justify-content: space-between;

    background-color: var(--error-color);
    color: var(--secondary-color);
    opacity: 0.8;
    font-size: 1.6em;
    width: 100%;
    position: absolute;
    /*padding: 0.2em;*/
    top: calc(var(--header-height) * 1.2);

}

.context.open {
    max-height: 40vh;
}
