.keyboard{
    display: flex;
    flex-direction: column;
    gap: 0.4em;

}
.row{
    display: flex;
    justify-content: center;
    gap: 0.4em;
}

.keyboard input{
    font-size: 2em;
    line-height: 1.4em;
    text-align: center;
    padding: 0;
    color: var(--text-color);
    font-weight: 500;
}

.btn{
    font-size: 1.4em;
    padding: 0.3em 1em;
}