.border {

    border: 1px solid var(--border-color);
    border-radius: 0.5em;
    cursor: pointer;
}
.hover_effect{
    box-shadow: 0 0 10px var(--shadow-color), 0 0 10px var(--shadow-color), 0 0 10px var(--shadow-color), 0 0 10px var(--shadow-color), 0 0 10px var(--shadow-color);
}
