.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;

    height: var(--header-height);
    gap: 0.4em;
    align-items: center;
    z-index: 5;
    background: var(--heading-color);
    color: var(--secondary-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 0.5em;
}
.wrapper_info{
    display: none;
}

.burger {
    display: none;
}


@media (max-width: 550px) {
    .header {
        height: var(--header-height);
        justify-content: flex-end;
        /*justify-content: space-between;*/
    }
    .header  input[type="text"] {
        width: 60vw;
    }

    .burger {
        display: block;
    }
    .wrapper_info{
        /*display: block;*/

        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 55vw;
        height: var(--header-height);

        position: absolute;
        /*top: 50%;*/
        left: 20%;

        z-index: 1;
        /*border: 1px solid red;*/
    }

}

