.scanner {

    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    background-color: inherit;
    padding-top: 20em;

    background-color: rgba(0, 0, 0, 0.4);
    padding-top: var(--header-height);

    position: fixed;
    top: 0;
    left: 0;

    transform: scale(0.5);
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;

    z-index: 2;

}

.scanner.visible{
    transform: scale(1);
    opacity: 1;
    pointer-events: all;
}
.wrapper_scanner{
    margin: 4em 0 0.5em;
    position: relative;
}

#qrCodeContainer {
    position: relative;
    border-radius: 2em;
    overflow: hidden;
    border: 1px solid var(--border-color);
    margin-bottom: 1em;
}

#qr-shaded-region {
    border-width: 50px !important;
}

video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto !important;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
}

.qr-message {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
}
.wrapper_message{
    position: absolute;
    top:50%;
    width: 80%;
    z-index: 100;
}

datalist{
    display: flex;
    justify-content: space-between;
    width: 50vw;
}

option{
    padding: 0;
}
input[type="range"]{
    width: 50vw;
    margin: 0;
}

.wrapper{
    position: relative;
}

button{
    border:1px solid var(--border-color);
    background-color: var(--accent-color);
    color: var(--error-color);
    padding: 0.5em;
    font-size: 1em;
    border-radius: 0.5em;
    padding: 1em;
    font-size: 1.8em;
}