.scanning{
    border-radius: 2em;
    position: absolute;
    top:0;
    overflow: hidden;

    transform-origin:50% 50% ;
    transform: scale(0);
    transition: transform 1s;
}

.scanning--entering{
    transform:scale(1);
}
.scanning--entered{
    transform:scale(1);
}

.scanning_inner{
    width: 100%;
    height: 10px;
    background: #35fd5c;
    filter: drop-shadow(0 0 5px #35fd5c ) drop-shadow(0 0 360px #35fd5c );
    animation-name: scanning;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}
@keyframes scanning{
    0% {
        transform: translateY(0);
    }
    100% {
        transform:translateY(300px);
    }
}
