:root {
    --header-height: 10vh;
    --header-min-height: 80px;
    --row-min-height: 3em;
    --mobile-width: 500px;
    --radius: 1em;
    --burger-z-index: 9;
    --navbar-children-width: 20vw;
    --sidebar-width: 65vw;


    /*    color*/
    --primary-color: #3498db; /* Blue */
    --secondary-color: #F4F7FB; /* Green */
    --background-color: #F1F2F3; /* White */
    --text-color: #3E706D; /* Dark Gray */
    --heading-color: #2c3e50; /* Darker Gray */
    --border-color: #e0e0e0; /* Light Gray */
    --link-color: #DDA94B;
    --accent-color:#3E706D;
    --success-color: #28a745; /* Green */
    --error-color: #dc3545; /* Red */
    --warning-color: #ffc107; /* Yellow */
    --shadow-color:#9E9E9E;
}